




































































































export default {
	data() {
		var gid='';
		var attribute={};
		return {
			datas:[],
			banner:{
				indicatorColor:'#FFFFFF',
				indicatorActiveColor:'#F6156D',
				circular:true,
				indicatorDots:true 
			},
			active:false,
			spec_index:'',
			goods_num:1
		};
	},
	
	onLoad:function(e){
		var token=(uni.getStorageSync("user_info")).token;
		uni.request({
			url: 'http://bus.liebianzhe.com/api/order/goods_details',
			method: 'POST',
			data: {
				token:token,
				gid:e.id
			},
			success: res => {
				console.info(res.data);
				console.log('好嗨哦');
				if(res.data.code==1){
					this.datas=res.data.data;
					this.gid=res.data.data.id;
				}else if(res.data.code==10){
					uni.removeStorage({
						key:'user_info',
					})
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
					setTimeout(function(){
						uni.reLaunch({
							url:'../../login/login'
						})
					},1500)
					
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
				}
			},
			fail: () => {
				console.info("请求失败~");
			},
		});
	},
	methods:{
		popup_active(){
			this.active=!this.active;
		},
		goods_popup_spec(index1,index){
			this.spec_index=index1;
			var attribute={};
			attribute[index]=index1;
			this.attribute=JSON.stringify(attribute);
		},
		reduce(){
			this.goods_num==1?this.goods_num=1:this.goods_num=parseInt(this.goods_num)-1
		},
		add(){
			this.goods_num=parseInt(this.goods_num)+1;
		},
		exchange(){
			var gid=this.gid,
				attribute=this.attribute,
				goods_num=this.goods_num,
				token=(uni.getStorageSync("user_info")).token;;
			if(attribute==null){
				uni.showToast({
					title: '请先选择规格',
					mask: false,
					duration: 1500,
					icon:'none'
				});
			}else{
				uni.request({
					url: 'http://bus.liebianzhe.com/api/order/place_order',
					method: 'POST',
					data: {
						token:token,
						gid:gid,
						num:goods_num,
						attribute:attribute
					},
					success: res => {
						if(res.data.code==1){
							console.info(res.data);
							uni.setStorageSync('oreder_num',res.data.data);
							uni.showToast({
								title: res.data.msg,
								mask: false,
								duration: 1500,
								icon:'none'
							});
							setTimeout(function(){
								uni.navigateTo({
									url: '../confirm-oreder/confirm-oreder?address_type=0'
								});
							},2000)
						}else{
							uni.showToast({
								title: res.data.msg,
								mask: false,
								duration: 1500,
								icon:'none'
							});
						}
					},
					fail: () => {
						console.info("请求失败~")
					},
				});
			}
		}
	}
}
